let uniqueGlobalName = (() => {
    let freeid = 0;
    return thing => {
        let id = `gmx_unique_${freeid++}`;
        window[id] = thing;
        return id;
    };
})();


/** ÐÐ¾ÑÑÐ»Ð°ÐµÑ ÐºÑÐ¾ÑÑ-Ð´Ð¾Ð¼ÐµÐ½Ð½ÑÐ¹ GET Ð·Ð°Ð¿ÑÐ¾Ñ Ðº ÑÐµÑÐ²ÐµÑÑ Ñ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°Ð½Ð¸ÐµÐ¼ ÑÑÐ°Ð½ÑÐ¿Ð¾ÑÑÐ° JSONP.
 *
 * @memberOf nsGmx.Utils
 * @param {String} url URL ÑÐµÑÐ²ÐµÑÐ°.
 * @param {Function} callback Ð¤-ÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð±ÑÐ´ÐµÑ Ð²ÑÐ·Ð²Ð°Ð½Ð° Ð¿ÑÐ¸ Ð¿Ð¾Ð»ÑÑÐµÐ½Ð¸Ð¸ Ð¾Ñ ÑÐµÑÐ²ÐµÑÐ° ÑÐµÐ·ÑÐ»ÑÑÐ°ÑÐ°.
 * @param {String} [callbackParamName=CallbackName] ÐÐ¼Ñ Ð¿Ð°ÑÐ°Ð¼ÐµÑÑÐ° Ð´Ð»Ñ Ð·Ð°Ð´Ð°Ð½Ð¸Ñ Ð¸Ð¼ÐµÐ½Ð¸ Ñ-ÑÐ¸Ð¸ Ð¾ÑÐ²ÐµÑÐ°.
 * @param {Function} [errorCallback] Ð¤-ÑÐ¸Ñ, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð±ÑÐ´ÐµÑ Ð²ÑÐ·Ð²Ð°Ð½Ð° Ð² ÑÐ»ÑÑÐ°Ðµ Ð¾ÑÐ¸Ð±ÐºÐ¸ Ð·Ð°Ð¿ÑÐ¾ÑÐ° Ðº ÑÐµÑÐ²ÐµÑÑ
 */
function sendCrossDomainJSONRequest (url, callback, callbackParamName, errorCallback) {
	callbackParamName = callbackParamName || 'CallbackName';

    let script = document.createElement("script");
	script.setAttribute("charset", "UTF-8");
	let callbackName = uniqueGlobalName(obj => {
		callback && callback(obj);
		window[callbackName] = false;
		document.getElementsByTagName("head").item(0).removeChild(script);
	});

    let sepSym = url.indexOf('?') == -1 ? '?' : '&';

    if (errorCallback) {
        script.onerror = errorCallback;
    }

	script.setAttribute("src", `${url}${sepSym}${callbackParamName}=${callbackName}&${Math.random()}`);
	document.getElementsByTagName("head").item(0).appendChild(script);
}

export { sendCrossDomainJSONRequest };